export interface VideoListProps {
  list: CreatorVideoItem[];
  sortMode?: boolean;
  trycount?:number;
  price?:string;
  payProps?: Partial<StripePayAreaProps>;
  onClick: (item: CreatorVideoItem) => void;
  onEdit?: (item: CreatorVideoItem) => void;
  onDelete?: (item: CreatorVideoItem) => void;
  onSort?: (items: CreatorVideoItem[]) => void;
}

export const VideoList: FC<VideoListProps> = ({ payProps = {}, list,sortMode,price = '0.00',trycount = 0, onClick, onEdit, onDelete, onSort }) => {
  const rendItem = (it: any,i:number) => (
    <div className="video-item" key={it.id}

      onMouseDown={(e) => e.preventDefault()}
      onClickCapture={(e) => onSort && e.stopPropagation()}
    >
      {sortMode && <img className="reorder" src={reorderImg} alt="" />}
      <Image className="thumb" src={it.thumb} alt={it.thumb} onClick={() => onClick(it)} />
      <div className="item-info">
        <div className="top">
          <div className="title">
            {it.title}
          </div>
          <div className="free">{(i + 1) > trycount ? `$${price}` : 'FREE'}</div>
        </div>

        <div className="bottom">
          <div className="count">
            <span>{it.viewcount} {translate('creator.playlist.25')}</span>
          </div>
          <div className="date">
            Uploaded on {moment(it.createtime, 'YYYY-MM-DD HH:mm:ss').format('MM/DD/YYYY')}
          </div>
        </div>


      </div>
      <div className="tool-btns">
        {onEdit && <a onClick={() => onEdit(it)}>{IconEdit}</a>}
        {onDelete && (
          <Popconfirm
            placement="topRight"
            title={translate('creator.playlist.13')}
            onConfirm={() => onDelete(it)}
          >
            <a>{IconDelete}</a>
          </Popconfirm>
        )}
      </div>
    </div>
  );

  if (!list.length) {
    return <div className="video-list-no-data"><NoData text={translate('creator.playlist.24')} /></div>
  }

  if (onSort) {
    const sortCollectionOrder = (sourceId: string, sourceIndex: number, targetIndex: number, targetId?: string) => {
      const res = swap<CreatorVideoItem>(list, sourceIndex, targetIndex);
      console.log(res.map(it => it.title));
      onSort(res);
    };
    var fontSize = parseFloat(document.documentElement.style.fontSize.replace('px', ''));
    var height = 121 * (fontSize / 16);
    return (
      <GridContextProvider onChange={sortCollectionOrder}>
        <GridDropZone
          id="items"
          boxesPerRow={1}
          className="video-list"
          rowHeight={height}
        >
          {list.map((it, i) => (
            <GridItem key={it.id} className="video-item-wrap">{rendItem(it, i)}</GridItem>
          ))}
        </GridDropZone>
      </GridContextProvider>
    );
  }

  return (
    <div className="video-list">
      {list.map((it, i) => <div className="video-item-wrap" key={it.id}>{rendItem(it, i)}</div>)}
    </div>
  );
}