import { useEffect, useState } from 'react';
import { callbackify } from 'util';
import { Ajax } from '../utils/http';

export interface StockLogoProps{
    symbol?:string
}
let getLogo = async(symbol?: string,callback?:Function) =>{
    let response;
    try {
    response = await new Ajax(`https://aolinews.oss-ap-southeast-2.aliyuncs.com/logos/${symbol}.png`,{},false,true).get();
} catch (e) {
    callback && callback(false);
    return ;
} 
    callback && callback(true);
}

export const StockLogo = (props:StockLogoProps) => {
    const [showRemote, setRemote] = useState(true);
    useEffect(  () => {
        getLogo(props.symbol,(result:any)=>{
            setRemote(result);
        })
    }, []);
    
    return (
            <div >
               {!showRemote? <div className="icon">{(props.symbol || '')[0]}</div>:
                <img className="icon" src={`https://aolinews.oss-ap-southeast-2.aliyuncs.com/logos/${props.symbol}.png`} alt="" />}
            </div>
    )
}

