import './Creator.scss';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { creatorStore } from '../../stores/CreatorStore';
import { CommentPage } from './Comment';



export const CreatorDetail = observer(() => {
    return (
        <div className="creatorTopWrapper">
            <div className="top"></div>
            <div className="creatorNewsDetailWrapper">
                <div className="creatorNewsDetailContent" >
                    <div className="info">
                        <div className="title">{creatorStore.detail?.title}</div>
                        <div className="bottom">
                            <div className="date">{creatorStore.detail?.createtime.substring(0,16)}  </div>
                            <div className="author">{creatorStore.detail?.author}</div>
                        </div>
                    </div>
                    <div className="detailContent">
                        <div className="content" dangerouslySetInnerHTML={{ __html: creatorStore.detail?.content || '' }}></div>
                           <CommentPage objectid={ true?'34':creatorStore.detail?.id || '0'} objecttype={5}/>
                    </div>
                </div>
            </div>
        </div>
    )
});